
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.index-panel .head {
  margin-bottom: 30px;
}
.index-panel .head .top-banner {
  width: 100%;
  height: 48.5vw;
  position: relative;
}
.index-panel .head .top-banner .t-b-right-btn {
  width: 43px;
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -24px;
}
.index-panel .head .head-text-cell {
  margin-top: -30px;
  position: relative;
}
.index-panel .head .head-text-cell .head-text-view {
  background-color: #fff;
  border: 1px solid #555;
  display: inline-block;
  min-width: 50%;
  border-radius: 30px;
  padding: 10px 60px;
  line-height: 1em;
  font-size: 26px;
}
.index-panel .head .head-text-cell .head-text-view span {
  vertical-align: middle;
}
.index-panel .head .head-text-cell .head-text-view .text-spacing {
  letter-spacing: .25em;
}
.index-panel .head .head-text-cell .head-text-view .time-num {
  background-color: rgba(0, 0, 0, 0.5);
  color: #fcfcfc;
  font-size: .9em;
  padding: 2px 4px;
  display: inline-block;
  line-height: normal;
}
.index-panel .main .instruction-cell {
  font-size: 26px;
  position: relative;
  padding-top: 10px;
  margin: 20px 16px 60px;
}
.index-panel .main .instruction-cell .i-c-title {
  background-color: #ff9000;
  font-size: .95em;
  border-radius: 30px;
  padding: 4px 10px;
  line-height: 1em;
  position: absolute;
  left: 24px;
  top: 0;
}
.index-panel .main .instruction-cell .i-c-text {
  background-color: #ff3333;
  line-height: 1.7em;
  padding: 40px 36px 20px;
  border-radius: 16px;
}
.index-panel .m-top-count, .index-panel .user-list, .index-panel .user-list .user-item {
  padding: 0 22px;
}
.index-panel .user-list {
  margin: 20px 0;
}
.index-panel .user-list .user-item {
  overflow: hidden;
}
.index-panel .user-list .user-item .user-pic {
  position: relative;
  max-width: 100%;
  font-size: 0;
  margin-bottom: 16px;
}
.index-panel .user-list .user-item .user-pic .users-main-text {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 25px;
}
.index-panel .user-list .user-item .user-pic .user-avatar {
  max-width: 100%;
  border-radius: 50%;
  border: 1px solid #ddd;
  height: calc(20vw - 9px - 44px);
}
.index-panel .user-list .user-item .user-type-icon {
  width: 27px;
  max-height: 27px;
}
.index-panel .main .m-top-count .users-count {
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.45);
  border-radius: 12px;
  font-size: 12px;
  line-height: 1em;
  padding: 0 20px;
  color: #fff;
}
.index-panel .main .m-top-count .users-count span {
  padding: 4px 0;
  display: inline-block;
}
.index-panel .main .m-top-count .users-count:after {
  content: " ";
  display: inline-block;
  height: 6px;
  width: 6px;
  border-width: 1px 1px 0 0;
  border-color: #fff;
  border-style: solid;
  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  position: relative;
  /*top:-2px;*/
  /*margin-top:-4px;
      right:2px;*/
  vertical-align: middle;
}
.index-panel .main .image-list > img {
  width: 100%;
  display: block;
  margin: 20px auto;
}
.index-panel .main .image-list .arrow-icon {
  width: 31px;
}
.index-panel .main .main-btn {
  padding: 0 44px;
  margin: 50px 0;
}
.index-panel .main .main-btn button {
  font-size: 40px;
  border-radius: 20px;
  line-height: 2.8em;
}
.index-panel .lottery-cell .lottery-item {
  padding: 10px 0;
  margin: 20px 0;
}
.index-panel .lottery-cell .lottery-item .l-i-head {
  margin-bottom: 14px;
}
.index-panel .lottery-cell .lottery-item .l-i-head .l-i-h-id {
  display: inline-block;
  padding: 2px 8px;
  background-color: #ff4747;
  color: #fff;
  font-size: 12px;
  border-radius: 2px;
  line-height: 1em;
}
.index-panel .lottery-cell .lottery-item .l-i-head .l-i-h-right {
  display: inline-block;
  padding: 4px 8px 4px 10px;
  background-color: #ff4747;
  color: #fff;
  font-size: 14px;
  line-height: 1em;
  -moz-border-radius-topleft: 20px;
  -moz-border-radius-bottomleft: 20px;
  -o-border-radius-topleft: 20px;
  -o-border-radius-bottomleft: 20px;
  -webkit-border-top-left-radius: 20px;
  -webkit-border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}
.index-panel .lottery-list .lottery-item .l-i-main {
  padding: 10px 20px;
}
.index-panel .l-i-m-num-list .l-i-m-num-item {
  /*display: inline-block;*/
  margin: 0 6px;
  width: 41px;
  max-width: 82px;
  height: 42px;
  position: relative;
}
.index-panel .l-i-m-num-list .l-i-m-num-item span {
  display: block;
  line-height: 40px;
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 34px;
  margin-top: -21px;
  width: 100%;
  text-align: center;
  color: #fff;
}
.index-panel .l-i-m-num-list .l-i-m-num-list-right {
  text-align: right;
}



