
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.infopage .content-wrapper, .infopage .content-wrapper .content {
  margin-left: auto;
  margin-right: auto;
}
.infopage {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1100 !important;
  width: 100%;
  height: 100%;
  background: #fff;
}
.infopage .content-wrapper {
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}
.infopage .content-wrapper .content {
      width: 92%;
}
.infopage .content-wrapper .title {
      font-size: .36rem;
      font-weight: bold;
      line-height: .72rem;
      color: #1F2D3D;
}
.infopage .content-wrapper .information .userinfo {
      width: 2.5rem;
}
.infopage .content-wrapper .information .userinfo .avatar {
        width: .66rem;
        height: .66rem;
        border-radius: 100%;
        overflow: hidden;
}
.infopage .content-wrapper .information .userinfo .avatar img {
          width: .66rem;
          height: .66rem;
}
.infopage .content-wrapper .information .userinfo .username {
        color: #1F2D3D;
        margin-left: .16rem;
}
.infopage .content-wrapper .information .time {
      text-align: end;
      color: #D3DCE6;
}
.infopage .mu-appbar {
    color: #1F2D3D !important;
    background: #fff !important;
}
.infopage .mu-appbar .mu-icon-button {
      color: #1F2D3D !important;
}
.infopage .mu-appbar .btn-comment {
      width: 3.8rem;
      height: 42px;
      line-height: 42px;
      font-size: .28rem;
      color: #b2c2d4;
      background: #EFF2F7;
}
.infopage .mu-appbar .btn-reply {
      position: relative;
}
.infopage .mu-appbar .btn-reply .reply-count {
        position: absolute;
        right: .08rem;
        top: .02rem;
        width: .35rem;
        height: .35rem;
        line-height: .35rem;
        border-radius: 100%;
        background: #e96900;
        text-align: center;
        font-size: .2rem;
        color: #fff;
}
.infopage .mu-appbar .collected {
      color: #fcc015;
}



