
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.message {
  background: #fff;
}
.message .login {
    margin-top: 2rem;
}
.message .login .img {
      width: 1.5rem;
      height: 1.5rem;
}
.message .login .title {
      font-size: .32rem;
      color: #475669;
}
.message .login .button-wrapper {
      width: 2.5rem;
}
.message .content .new-msgs,
  .message .content .past-msgs {
    width: 100%;
}
.message .content .new-msgs .title,
    .message .content .past-msgs .title {
      position: relative;
      width: 100%;
      height: .66rem;
      line-height: .66rem;
      background: #EFF2F7;
}
.message .content .new-msgs .title:before,
      .message .content .past-msgs .title:before {
        content: '';
        display: block;
        width: 6px;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: #D3DCE6;
}
.message .content .new-msgs .title .btn-mark-all,
      .message .content .past-msgs .title .btn-mark-all {
        position: absolute;
        right: .32rem;
        top: 0;
        font-size: .22rem;
        color: teal;
}
.message .content .new-msgs .lists .tip,
    .message .content .past-msgs .lists .tip {
      width: 100%;
      text-align: center;
}
.message .content .new-msgs .lists .list,
    .message .content .past-msgs .lists .list {
      width: 100%;
      padding: .32rem;
      box-sizing: border-box;
      color: #475669;
      border-bottom: 1px solid #EFF2F7;
}
.message .content .new-msgs .lists .list:last-child,
      .message .content .past-msgs .lists .list:last-child {
        border-bottom: none;
}
.message .content .new-msgs .lists .list .username,
      .message .content .new-msgs .lists .list .time,
      .message .content .past-msgs .lists .list .username,
      .message .content .past-msgs .lists .list .time {
        color: #b1bdcb;
}
.message .content .new-msgs .lists .list .body,
      .message .content .past-msgs .lists .list .body {
        margin-top: .12rem;
}
.message .content .past-msgs .lists .list .time {
    color: #d0d7df !important;
}
.message .content .past-msgs .lists .list .body {
    color: #d0d7df;
}



