
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.mu-snackbar {
  height: 56px;
}
.snackbar-warn {
  background: #e96900 !important;
}
.snackbar-suc {
  background: teal !important;
}



