
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.replyItem {
  position: relative;
  padding: .24rem .32rem;
  margin-bottom: .12rem;
  box-sizing: border-box;
  background: #fff;
}
.replyItem .thumb-up-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 20%;
}
.replyItem .thumb-up-wrapper .icon {
      color: #D3DCE6;
      font-size: .38rem;
}
.replyItem .thumb-up-wrapper .ups-count {
      font-size: .24rem;
      color: #475669;
      margin-left: -.18rem;
}
.replyItem .avatar {
    width: .88rem;
    height: .88rem;
    border-radius: 100%;
    overflow: hidden;
}
.replyItem .avatar img {
      width: .88rem;
      height: .88rem;
}
.replyItem .info-wrapper {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-left: .24rem;
}
.replyItem .info-wrapper .username {
      font-weight: bold;
}
.replyItem .info-wrapper .content {
      margin-top: -.12rem;
}
.replyItem .info-wrapper .reply-time {
      margin-top: .12rem;
      font-size: .2rem;
      color: #7588a1;
}



