
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.repliesPage {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1100;
  width: 100%;
  height: 100%;
  background: #EFF2F7;
}
.repliesPage .mu-snackbar {
    height: 56px;
    bottom: 0;
}
.repliesPage .snackbar-warn {
    background: #e96900 !important;
}
.repliesPage .snackbar-suc {
    background: teal !important;
}
.repliesPage .title {
    position: relative;
    width: 100%;
    padding: .32rem;
    box-sizing: border-box;
}
.repliesPage .title:before {
      display: block;
      content: '';
      position: absolute;
      left: .32rem;
      top: 50%;
      -webkit-transform: translate3d(0, -50%, 0);
              transform: translate3d(0, -50%, 0);
      width: 4px;
      height: .28rem;
      background: #e96900;
}
.repliesPage .content-wrapper {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}
.repliesPage .comment-bar-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1100;
    width: 100%;
    height: 100%;
}
.repliesPage .comment-bar-wrapper .close-bg {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.54);
}
.repliesPage .comment-bar-wrapper .comment-bar {
      position: absolute;
      top: 15%;
      left: 50%;
      -webkit-transform: translate3d(-50%, 0, 0);
              transform: translate3d(-50%, 0, 0);
      width: 80%;
      height: 4rem;
      background: #fff;
}
.repliesPage .comment-bar-wrapper .comment-bar textarea {
        display: block;
        width: 92%;
        height: 2.6rem;
        margin-bottom: .24rem;
        outline: none;
        resize: none;
        border: none;
        background: #f6f8fa;
}
.repliesPage .mu-appbar {
    color: #1F2D3D !important;
    background: #fff !important;
}
.repliesPage .mu-appbar .btn-comment {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      height: 42px;
      line-height: 42px;
      font-size: .28rem;
      color: #b2c2d4;
      background: #EFF2F7;
}



